.data-input{
    font-size: 24pt;
    position: absolute;
}

.data-input.vertical{
    display: flex;
    flex-direction: row;
}   

.data-input.vertical .data-input__input__value{
    writing-mode: vertical-rl;
    text-orientation: mixed;
    text-align: center;
    transform: rotate(180deg);
}

.data-input__input{
    display: flex;
    justify-content: center;
}

.data-input__input__value{   
    font-size: 24pt;
    margin-right: 10px;
}

.data-input .rangeslider-horizontal{
    margin-top: 15px;

    width: 200px;
    height: 7px;
    border-radius: 8px;
    background-color: #000;
}

.data-input .rangeslider-vertical{
    margin-right: 15px;

    width: 7px;
    height: 200px;
    border-radius: 8px;
    background-color: #000;
}

.data-input .rangeslider__fill{
    background-color: #000;
}

.data-input .rangeslider__handle{
    background-color: #fff;

    width: 13px;
    height: 13px;

    outline: none;

    box-shadow: none;
    border: 2px solid #000;
}

.data-input.vertical .rangeslider__handle{
    transform: translate(25%, 0);

    border-radius: 100%;
}



.data-input .rangeslider__handle::after{
    content: none;
}