.info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

}

.info-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 5px;
    padding: 15px 0;
    cursor: pointer;
    margin: 0 10px;
}

.info-item__name{
    font-size: 35pt;
    font-weight: bold;
    
    display: flex;
    align-items: flex-end;
}

.info-item__data{
    font-size: 20pt;
    font-weight: normal;

    margin: 15px 0 15px 10px;
}

.info-item__unit{
    font-size: 20pt;
    font-weight: normal;
}

.info-item__name__index{
    font-size: 20pt;
    font-weight: normal;
}