.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    transition: background-color 0.5s ease-in-out;
    padding: 15px;
}

.data-title{
    font-size: 20px;
}

.input{
    margin-top: 10px;
}

.input span{
    display: inline-block;
    width: 20px;
    text-align: center;
}

.input span:nth-child(3){
    font-weight: bold;
}

.input input{
    width: 150px;
    margin: 0 10px 0 10px;
}

input[type="submit"]{
    
    margin-top: 50px;
    width: 150px;
}

.model-input{
    display: flex;
    align-items: center;
}

.model-input img{
    width: 350px;
    height: 193px;
}

.data_input{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}