.model{
    --svg-offset: 200px;

    width: calc(466px + var(--svg-offset));
    min-height: calc(195px + var(--svg-offset));

    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: var(--svg-offset);

    position: relative;
}

svg{
    width: 466px;
    height: 195px;
}

.model-resist{
    width: 28px;
    height: 120px;
    border: 3px solid black;
    border-radius: 5px;
}

.model-capacity{
    display: flex;

}

.model-capacity__item{
    width: 5px;
    height: 85px;
    background-color: #000;
}

.model-capacity__item:nth-child(1){
    margin-right: 5px;
}

.model-inductance{
    width: 200px;
    height: 37.5px;
    overflow: hidden;

    display: flex;
    flex-direction: row;
}

.model-inductance__item{
    width: 75px;
    height: 75px;
    border-radius: 100%;
    border: 8px solid #000;
}

#path{
   stroke-width: 50;
}

.model-resistor{
    width: 25px;
    height: 100px;
    background-color: #fff;
    border: 3px solid #000;
    left: 87px;
    position: absolute;
}

.model-capacity{
    width: 25px;
    height: 56px;
    display: flex;
    position: absolute;
    top: 75px;
    justify-content: space-between;
    background-color: #fedc32;
}

.model-capacity__item{
    width: 10px;
    height: 100%;
    background-color: #000;
}

.model-eds{
    width: 50px;
    height: 50px;
    position: absolute;
    background-color: #fff;
    border: 3px solid #000;
    border-radius: 100px;

    line-height: 50px;
    text-align: center;
    font-size: 32px;
    right: 75px;
}